import { HttpClient, HttpHeaders, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { environment } from "../../environments/environment";
import { User } from "../models/user";

@Injectable()
export class InfoService {
    baseUrl = environment.apiUrl;
    constructor(private http: HttpClient) { }

    getBasicInfo(input): any {
        const formData: FormData = new FormData();
        formData.append("CompGUID", input);

        return this.http.post(this.baseUrl + "getBasicInfo", formData);
    }

    setBasicInfo(user): any {
        console.log("user", user);
        const formData: FormData = new FormData();
        formData.append("CompGUID", user.compGUID);
        formData.append("CompName", user.compName);
        formData.append("CompCountryId", user.location);
        formData.append("CompSectorId", user.coSector);
        formData.append("CompEmployees", user.compEmployees);
        formData.append("CompMarketId", user.companyType);
        formData.append("UserFirstName", user.userFirstName);
        formData.append("UserLastName", user.userLastName);
        formData.append("UserEmail", user.userEmail);
        formData.append("UserBirthYear", user.userBirthYear);
        formData.append("UserDepartmentId", user.userDepartmentId);
        formData.append("UserFunction", user.userFunction);
        formData.append("UserNewsletter", user.userNewsletter);

        return this.http.post(this.baseUrl + "setBasicInfo", formData);
    }

    getContent(input): any {
        const formData: FormData = new FormData();
        formData.append("intPage", input);

        return this.http.post(this.baseUrl + "getContent", formData);
    }

    getQuestion(input): any {
        const formData: FormData = new FormData();
        formData.append("intDimension", input.intDimension);
        formData.append("intIndicator", input.intIndicator);
        formData.append("intStep", input.intStep);

        return this.http.post(this.baseUrl + "getQuestion", formData);
    }

    setAnswer(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input.userGUID);
        formData.append("QuestionId", input.QuestionId);
        formData.append("QuestionAnswer", input.QuestionAnswer);

        return this.http.post(this.baseUrl + "setAnswer", formData);
    }

    getResult(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input);

        return this.http.post(this.baseUrl + "getResult", formData);
    }
    getResultPDF(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input);

        return this.http.post(this.baseUrl + "getResultPDF", formData);
    }
    setContinueLater(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input);

        return this.http.post(this.baseUrl + "setContinueLater", formData);
    }
    getContinue(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input);

        return this.http.post(this.baseUrl + "getContinue", formData);
    }

    setInvite(input, userID): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", userID);
        formData.append("Name", input.name);
        formData.append("Email", input.email);
        formData.append("Options", input.company);
        formData.append("Note", input.note);

        return this.http.post(this.baseUrl + "setInvite", formData);
    }

    getInviteLink(input): any {
        const formData: FormData = new FormData();
        formData.append("UserGUID", input);

        return this.http.post(this.baseUrl + "getInviteLink", formData);
    }
}
