import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { NgbModal, NgbModalConfig } from "@ng-bootstrap/ng-bootstrap";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../../services/info.service";
import { ModalDialogComponent } from "../dimension/modal-dialog/modal-dialog.component";

@Component({
    selector: "app-intro",
    templateUrl: "./intro.component.html",
    styleUrls: ["./intro.component.scss"]
})
export class IntroComponent implements OnInit {
    introContent;
    getContent;
    questionnaire: boolean = false;
    getUserGUID;
    constructor(
        private spinner: NgxSpinnerService,
        private route: ActivatedRoute,
        private router: Router,
        private infoService: InfoService,
        private modalService: NgbModal,
        private modalConfig: NgbModalConfig
    ) {
        this.modalConfig.backdrop = "static";
        this.modalConfig.keyboard = false;
        this.route.params.subscribe((params) => this.getContent = params.id);
        this.getUserGUID = localStorage.getItem("UserID");
    }

    ngOnInit() {
        if (this.getContent < 4) {
            localStorage.removeItem("dimension");
        }
        this.getContents();
    }
    getContents() {
        this.spinner.show();
        this.infoService.getContent(this.getContent).subscribe((res) => {
            const pageHTML = "pageHTML ";
            this.introContent = res[pageHTML];
            this.spinner.hide();
        }, (error) => {
            this.spinner.hide();
        });
    }
    next() {
        if (this.getContent && this.getContent < 3) {
            this.getContent++;
            this.router.navigate([`intro/${this.getContent}`]);
            this.getContents();
            this.questionnaire = true;
        } else {
            this.startQuestionnaire();
        }
    }
    startQuestionnaire() {
        this.router.navigate([`questionnaire/${this.getContent++}`]);
    }
    open() {
        this.modalService.open(ModalDialogComponent, { centered: true });
    }

    continueLater(option) {
        localStorage.setItem("modal", option);
        this.open();
    }
}
