import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../../services/info.service";

@Component({
    selector: "app-redirect",
    templateUrl: "./redirect.component.html",
    styleUrls: ["./redirect.component.scss"]
})
export class RedirectComponent implements OnInit {
    page;
    pageNum;
    userID;
    dimension;
    indicator;
    step;
    isRedirect = "1";
    compGUID;
    constructor(
        private spinner: NgxSpinnerService,
        private route: ActivatedRoute,
        private router: Router,
        private infoService: InfoService
    ) {
        this.route.queryParams.subscribe((params) => {
            const user = "userGUID";
            this.userID = params[user];
            this.page = params.p;
            this.dimension = params.Dim;
            this.indicator = params.Ind;
            this.step = params.Step;
            this.compGUID = params.compGUID;
            console.log(this.compGUID);
            localStorage.setItem("compGUID", this.compGUID);
        });
    }
    ngOnInit() {
        if (this.compGUID) {
            this.router.navigate(["info"]);
        }
        if (this.userID !== "" && this.userID !== undefined) {
            localStorage.clear();
            this.infoService.getContinue(this.userID).subscribe((res) => {
                localStorage.setItem("UserID", this.userID);
                localStorage.setItem("pageId", this.page);
                localStorage.setItem("dimensions", this.dimension);
                localStorage.setItem("indicator", this.indicator);
                localStorage.setItem("nextStep", this.step);
                localStorage.setItem("redirect", this.isRedirect);
                if (this.dimension === "1" && this.dimension !== undefined) {
                    this.router.navigate([`questionnaire/3`]);
                } else if (this.dimension === "2" && this.dimension !== undefined) {
                    this.router.navigate([`questionnaire/4`]);
                } else if (this.dimension === "3" && this.dimension !== undefined) {
                    this.router.navigate([`questionnaire/5`]);
                } else {
                    this.router.navigate(["welcome"]);
                }
            });
        }
    }


}
