import { Component, Input, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../../../services/info.service";

@Component({
    selector: "app-modal",
    templateUrl: "./modal-dialog.component.html",
    styleUrls: ["./modal-dialog.component.scss"]
})
export class ModalDialogComponent implements OnInit {
    getUserGUID;
    continueMessage;
    continue;
    indExp: boolean = false;
    quesExp: boolean = false;
    modalValue;
    @Input() indicatorExplanation;
    @Input() questionExplanation;
    constructor(
        private spinner: NgxSpinnerService,
        private router: Router,
        public activeModal: NgbActiveModal,
        private infoService: InfoService
    ) { this.getUserGUID = localStorage.getItem("UserID"); }

    ngOnInit() {
        this.modalValue = localStorage.getItem("modal");
        if (this.modalValue === "continue") {
            this.quesExp = false;
            this.indExp = false;
            this.continue = true;
            this.infoService.setContinueLater(this.getUserGUID).subscribe((later) => {
                const returnURL = "returnURL ";
                this.continueMessage = later[returnURL];
            });
        } else if (this.modalValue === "indicator") {
            this.indExp = true;
            this.quesExp = false;
            this.continue = false;
        } else if (this.modalValue === "question") {
            this.quesExp = true;
            this.indExp = false;
            this.continue = false;
        }
    }
    close() {
        localStorage.removeItem("modal");
        this.activeModal.dismiss();
    }

    dismiss() {
        localStorage.removeItem("explain");
        this.activeModal.dismiss();
    }
}
