import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../services/info.service";

@Component({
    selector: "app-thanks",
    templateUrl: "./thanks.component.html",
    styleUrls: ["./thanks.component.scss"]
})
export class ThanksComponent implements OnInit {
    introContent;
    getContent = 6;
    constructor(
        private spinner: NgxSpinnerService,
        private router: Router,
        private infoService: InfoService
    ) { }

    ngOnInit() {
        const userGUID = localStorage.getItem("UserID");
        if (userGUID) {
            localStorage.removeItem("dimensions");
            localStorage.removeItem("indicator");
            localStorage.removeItem("nextStep");
            localStorage.removeItem("pageId");
            this.getContents();
        } else {
            this.router.navigate(["welcome"]);
        }
    }

    getContents() {
        this.spinner.show();
        this.infoService.getContent(this.getContent).subscribe((res) => {
            const pageHTML = "pageHTML ";
            this.introContent = res[pageHTML];
            this.spinner.hide();
        }, (error) => {
            this.spinner.hide();
        });
    }

    getResult() {
        this.router.navigate(["result"]);
    }
}
