import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { NgbModal, NgbModalConfig } from "@ng-bootstrap/ng-bootstrap";
import { Chart } from "chart.js";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../services/info.service";
import { InviteModalComponent } from "./invite-modal/invite-modal.component";

@Component({
    selector: "app-result",
    templateUrl: "./result.component.html",
    styleUrls: ["./result.component.scss"]
})
export class ResultComponent implements OnInit {
    chart: any = [];
    // var myChart = new Chart(ctx, { ...});
    // Chart.defaults.scale.gridLines.color = "rgba(255,0,0,1)";
    // Chart.defaults.scale.gridLines.zeroLineColor = "rgba(255,0,0,1)";
    // const ctx = "myChart";
    dimension: boolean = false;
    dimension1: boolean = false;
    dimension2: boolean = false;
    dimension3: boolean = false;
    dimensionButton: boolean = false;
    dimension1Button: boolean = true;
    dimension2Button: boolean = false;
    dimension3Button: boolean = false;
    overallChartLabels;
    overallChartData;
    overallChartOptions;
    dimension1ChartLabels;
    dimension1ChartData;
    dimension1ChartOptions;
    dimension2ChartLabels;
    dimension2ChartData;
    dimension2ChartOptions;
    dimension3ChartLabels;
    dimension3ChartData;
    dimension3ChartOptions;
    chartColors: Array<any> = [
        {
            backgroundColor: "#1e8dfb",
            borderColor: "rgba(220,220,220,1)",
            borderWidth: 2,
            pointBackgroundColor: "rgba(220,220,220,1)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgba(220,220,220,1)"
        },
        {
            backgroundColor: "#f5ef67",
            borderColor: "rgba(220,220,220,1)",
            borderWidth: 2,
            pointBackgroundColor: "rgba(220,220,220,1)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgba(220,220,220,1)"
        },
        {
            backgroundColor: "#808182",
            borderColor: "rgba(220,220,220,1)",
            borderWidth: 2,
            pointBackgroundColor: "rgba(220,220,220,1)",
            pointBorderColor: "#fff",
            pointHoverBackgroundColor: "#fff",
            pointHoverBorderColor: "rgba(220,220,220,1)"
        }
    ];
    radarChartType: string = "radar";
    params;
    advice;
    inviteOthers: boolean = false;
    constructor(
        private spinner: NgxSpinnerService,
        private infoService: InfoService,
        private modalService: NgbModal,
        private router: Router,
        private modalConfig: NgbModalConfig
    ) {
        this.modalConfig.backdrop = "static";
        this.modalConfig.keyboard = false;
    }

    ngOnInit() {
        this.spinner.show();
        const userGUID = localStorage.getItem("UserID");
        if (userGUID) {
            this.infoService.getResult(userGUID).subscribe((res) => {
                const advice = "resultText";
                const resultDimensions = "resultDimensions";
                this.overallChartLabels = ["1", "2", "3"];
                this.overallChartData = [
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimensions][0].Comp, res[resultDimensions][0].Average, res[resultDimensions][0].High], label: "Your company" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimensions][1].Comp, res[resultDimensions][1].Average, res[resultDimensions][1].High], label: "5% highest" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimensions][2].Comp, res[resultDimensions][2].Average, res[resultDimensions][2].High], label: "Average" }
                ];
                this.overallChartOptions = {
                    pointDot: false,
                    pointLabelFontSize: 40,
                    responsive: true,
                    scale: {
                        pointLabels: {
                            fontSize: 15
                        },
                        reverse: false,
                        gridLines: {
                            display: true,
                            color: "#fff"
                        },
                        ticks: {
                            display: false,
                            backdropColor: "black",
                            maxTicksLimit: 100,
                            max: 50,
                            stepSize: 10,
                            beginAtZero: true,
                            fontSize: 10,
                            backdropPaddingX: 5,
                            backdropPaddingY: 5
                        }
                    },
                    legend: {
                        display: true,
                        position: "top",
                        fontSize: 15,
                        usePointStyle: true,
                        labels: {
                            fontColor: "#fff",
                            boxWidth: 20
                        }
                    }
                };
                const resultDimension1 = "resultDimension1";
                // tslint:disable-next-line:max-line-length
                this.dimension1ChartLabels = ["1", "2 ", "3"];
                this.dimension1ChartData = [
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension1][0].Comp, res[resultDimension1][0].Average, res[resultDimension1][0].High], label: "Your company" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension1][1].Comp, res[resultDimension1][1].Average, res[resultDimension1][1].High], label: "5% highest" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension1][2].Comp, res[resultDimension1][2].Average, res[resultDimension1][2].High], label: "Average" }
                ];
                this.dimension1ChartOptions = {
                    responsive: true,
                    scale: {
                        pointLabels: {
                            fontSize: 15
                        },
                        gridLines: {
                            display: true,
                            color: "#fff",
                            fontSize: 12
                        },
                        ticks: {
                            display: false,
                            backdropColor: "black",
                            maxTicksLimit: 100,
                            max: 50,
                            stepSize: 10,
                            beginAtZero: true,
                            fontSize: 10,
                            backdropPaddingX: 5,
                            backdropPaddingY: 5
                        }
                    },
                    legend: {
                        display: true,
                        labels: {
                            fontColor: "#fff"
                        }
                    }
                };
                const resultDimension2 = "resultDimension2";
                // tslint:disable-next-line:max-line-length
                this.dimension2ChartLabels = ["1", "2", "3"];
                this.dimension2ChartData = [
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension2][0].Comp, res[resultDimension2][0].Average, res[resultDimension2][0].High], label: "Your company" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension2][1].Comp, res[resultDimension2][1].Average, res[resultDimension2][1].High], label: "5% highest" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension2][2].Comp, res[resultDimension2][2].Average, res[resultDimension2][2].High], label: "Average" }
                ];
                this.dimension2ChartOptions = {
                    responsive: true,
                    scale: {
                        pointLabels: {
                            fontSize: 20
                        },
                        gridLines: {
                            display: true,
                            color: "#fff"
                        },
                        ticks: {
                            display: false,
                            backdropColor: "black",
                            maxTicksLimit: 100,
                            max: 50,
                            stepSize: 10,
                            beginAtZero: true,
                            fontSize: 10,
                            backdropPaddingX: 5,
                            backdropPaddingY: 5
                        }
                    }
                };
                const resultDimension3 = "resultDimension3";
                // tslint:disable-next-line:max-line-length
                this.dimension3ChartLabels = ["1", "2", "3"];
                this.dimension3ChartData = [
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension3][0].Comp, res[resultDimension3][0].Average, res[resultDimension3][0].High], label: "Your company" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension3][1].Comp, res[resultDimension3][1].Average, res[resultDimension3][1].High], label: "5% highest" },
                    // tslint:disable-next-line:max-line-length
                    { data: [res[resultDimension3][2].Comp, res[resultDimension3][2].Average, res[resultDimension3][2].High], label: "Average" }
                ];
                this.dimension3ChartOptions = {
                    responsive: true,
                    scale: {
                        pointLabels: {
                            fontSize: 15
                        },
                        gridLines: {
                            display: true,
                            color: "#fff"
                        },
                        ticks: {
                            display: false,
                            backdropColor: "black",
                            maxTicksLimit: 100,
                            max: 50,
                            stepSize: 10,
                            beginAtZero: true,
                            fontSize: 10,
                            backdropPaddingX: 5,
                            backdropPaddingY: 5
                        }
                    }
                };
                this.dimension = true;
                this.advice = res[advice];
                this.spinner.hide();
            });
        } else {
            this.router.navigate(["welcome"]);
        }
    }
    getDimensionResult() {
        this.dimension = true;
        this.dimension1 = false;
        this.dimension2 = false;
        this.dimension3 = false;
    }
    getDimension1Result() {
        this.dimension = false;
        this.dimension1 = true;
        this.dimension2 = false;
        this.dimension3 = false;
        this.dimension1Button = false;
        this.dimension2Button = true;
    }
    getDimension2Result() {
        this.dimension = false;
        this.dimension1 = false;
        this.dimension2 = true;
        this.dimension3 = false;
        this.dimension2Button = false;
        this.dimension3Button = true;
    }
    getDimension3Result() {
        this.dimension = false;
        this.dimension1 = false;
        this.dimension2 = false;
        this.dimension3 = true;
        this.dimension2Button = false;
        this.dimensionButton = true;
        this.dimension3Button = false;
    }
    open() {
        const modalRef = this.modalService.open(InviteModalComponent, { centered: true });
        modalRef.componentInstance.advice = this.advice;
    }
    inviteOther() {
        this.dimensionButton = false;
        this.inviteOthers = true;
    }
    invite(shareOptions) {
        localStorage.setItem("share", shareOptions);
        this.open();
    }
}
