import { Component, Input, OnInit } from "@angular/core";
import { NgbActiveModal, NgbModal, NgbModalConfig } from "@ng-bootstrap/ng-bootstrap";
import { ModalDialogComponent } from "../modal-dialog/modal-dialog.component";
import { RadioButton } from "./invite";

@Component({
    selector: "app-invite",
    templateUrl: "./invite-modal.component.html",
    styleUrls: ["./invite-modal.component.scss"]
})
export class InviteModalComponent implements OnInit {
    invite;
    radio = RadioButton;
    personal: boolean = false;
    others: boolean = false;
    adviceImprove: boolean = false;
    @Input() advice;
    constructor(
        public activeModal: NgbActiveModal,
        private modalService: NgbModal,
        private modalConfig: NgbModalConfig
    ) {
        this.modalConfig.backdrop = "static";
        this.modalConfig.keyboard = false;
        this.invite = {
            email: "",
            name: "",
            company: this.radio.same,
            note: ""
        };

    }
    ngOnInit() {
        const sharingOptions = localStorage.getItem("share");
        if (sharingOptions === "share") {
            this.others = true;
        } else if (sharingOptions === "personal") {
            this.personal = true;
        } else if (sharingOptions === "advice") {
            this.adviceImprove = true;
        }
    }

    submit() {
        console.log(this.invite);
        localStorage.setItem("invitation", JSON.stringify(this.invite));
        this.activeModal.close();
        setTimeout(() => {
            const modelRef = this.modalService.open(ModalDialogComponent, { centered: true });
            modelRef.componentInstance.note = this.invite.note;
            modelRef.componentInstance.email = this.invite.email;
        }, 50);
    }

    dismiss() {
        this.activeModal.dismiss();
    }
}
