import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { NgbModal, NgbModalConfig } from "@ng-bootstrap/ng-bootstrap";
import { Options } from "ng5-slider";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../../services/info.service";
import { ModalDialogComponent } from "./modal-dialog/modal-dialog.component";

@Component({
    selector: "app-dimension",
    templateUrl: "./dimension.component.html",
    styleUrls: ["./dimension.component.scss"]
})
export class DimensionComponent implements OnInit {
    value: number = 4;
    options: Options = {
        floor: 0,
        // showSelectionBar: true,
        // showTicks: true,
        translate: (value: number): string => {
            if (value === 0) {
                return "";
            }
            if (value === 1) {
                return "Disagree";
            }
            if (value === 2) {
                return "Neutral";
            }
            if (value === 3) {
                return "Agree";
            }
            if (value === 4) {
                return "";
            }
        }
    };
    indicatorExplanation;
    indicatorName;
    question;
    questionExplanation;
    nextIndicator;
    nextStep;
    questionId;
    getQuestion;
    answer;
    getContent;
    nextQuestion: boolean = true;
    nextDimension: boolean = false;
    onSubmit: boolean = false;
    isQuestionExpl: boolean = true;
    getUserGUID;
    isRedirect = false;
    constructor(
        private spinner: NgxSpinnerService,
        private route: ActivatedRoute,
        private router: Router,
        private infoService: InfoService,
        private modalService: NgbModal,
        private modalConfig: NgbModalConfig
    ) {
        this.modalConfig.backdrop = "static";
        this.modalConfig.keyboard = false;
        this.route.params.subscribe((params) => this.getContent = Number(params.id));
        this.getQuestion = {
            intDimension: 1,
            intIndicator: 1,
            intStep: 1
        };
        this.answer = {
            userGUID: "",
            QuestionId: "",
            QuestionAnswer: ""
        };
    }

    ngOnInit() {
        debugger;
        // const p = localStorage.getItem("pageId");
        const dimension = Number(localStorage.getItem("dimensions"));
        const indicator = localStorage.getItem("indicator");
        const step = localStorage.getItem("nextStep");
        // debugger;
        const isRedirect = localStorage.getItem("redirect");
        if (isRedirect === "1" && isRedirect !== undefined) {
            this.isRedirect = true;
            this.getQuestion.intDimension = dimension;
            this.getQuestion.intIndicator = indicator;
            this.getQuestion.intStep = step;
            localStorage.removeItem("redirect");
        }
        this.getUserGUID = localStorage.getItem("UserID");
        this.nextQuestion = true;
        if (this.getContent < 4) {
            localStorage.removeItem("dimension");
        }
        const intDimension = Number(localStorage.getItem("dimensions"));
        // tslint:disable-next-line:prefer-conditional-expression
        if (intDimension !== 0 && intDimension < 4) {
            this.getQuestion.intDimension = intDimension;
        } else {
            this.getQuestion.intDimension = 1;
        }
        const nextIndicator = Number(localStorage.getItem("indicator"));
        console.log(typeof nextIndicator);
        // tslint:disable-next-line:prefer-conditional-expression
        if (nextIndicator !== 1 && nextIndicator < 9) {
            this.getQuestion.intIndicator = nextIndicator;
            if (this.isRedirect === false) {
                // this.getQuestion.intIndicator++;
            }
        } else if (nextIndicator === 9) {
            this.getQuestion.intIndicator = nextIndicator;
        } else {
            this.getQuestion.intIndicator = 1;
        }
        if (this.getQuestion.intIndicator === 0) {
            this.getQuestion.intIndicator++;
        }
        this.getQuestions();
    }

    getQuestions() {
        this.spinner.show();
        this.infoService.getQuestion(this.getQuestion).subscribe((res) => {
            const indicatorExplanation = "IndicatorExplanation ";
            const indicatorName = "IndicatorName ";
            const question = "Question ";
            const questionExplanation = "QuestionsExplanation";
            const questionsId = "QuestionsId ";
            const nextIndicator = "NextIndicator ";
            const nextStep = "NextStep ";
            this.indicatorExplanation = res[indicatorExplanation];
            this.indicatorName = res[indicatorName];
            this.question = res[question];
            this.questionExplanation = res[questionExplanation];
            this.questionId = res[questionsId];
            this.nextIndicator = res[nextIndicator];
            this.nextStep = res[nextStep];
            setTimeout(() => {
                this.value = 2;
                this.spinner.hide();
            }, 5);
        }, (error) => {
            this.spinner.hide();
        });
    }
    open() {
        this.isQuestionExpl = false;
        const modelRef = this.modalService.open(ModalDialogComponent, { centered: true });
        modelRef.componentInstance.indicatorExplanation = this.indicatorExplanation;
        modelRef.componentInstance.questionExplanation = this.questionExplanation;
    }
    openModal(options) {
        localStorage.setItem("modal", options);
        this.open();
    }

    submit() {
        this.answer.userGUID = this.getUserGUID;
        this.answer.QuestionId = this.questionId;
        this.answer.QuestionAnswer = this.value;
        this.spinner.show();
        this.infoService.setAnswer(this.answer).subscribe((res) => {
            if (res.errNum === "200") {
                if (this.getContent === 5 && this.nextStep === "0" && this.nextIndicator === "9") {
                    setTimeout(() => {
                        this.router.navigate(["thanks"]);
                    }, 100);
                }
                // if (this.nextStep === "14" && this.getQuestion.intDimension === 3 && this.nextIndicator === "9") {
                //     this.nextDimension = false;
                //     this.nextQuestion = false;
                //     this.onSubmit = true;
                // } else if (this.nextStep === "14") {
                //     this.nextDimension = true;
                //     this.nextQuestion = false;
                //     this.onSubmit = false;
                // }
                if (this.nextStep === "0" && this.getContent !== 5) {
                    localStorage.setItem("dimensions", String(++this.getQuestion.intDimension));
                    localStorage.setItem("indicator", String(this.nextIndicator));
                    this.router.navigate([`intro/${++this.getContent}`]);
                }
                if (this.nextStep !== "0") {
                    this.getQuestion.intIndicator = this.nextIndicator;
                    this.getQuestion.intStep = this.nextStep;
                    this.getQuestions();
                }
                // this.value = 2;
            }
        }, (error) => {
            this.spinner.hide();
        });
    }
}
