import { HttpClientModule } from "@angular/common/http";
import { NgModule, NO_ERRORS_SCHEMA } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { BrowserModule } from "@angular/platform-browser";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { SelectModule } from "ng2-select";
import { NgxSelectModule } from "ngx-select-ex";
import { NgxSpinnerModule } from "ngx-spinner";
import { AppRoutingModule } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { InfoComponent } from "./component/basic-info/info.component";
import { DimensionModule } from "./component/dimensions/dimension.module";
import { ResultModule } from "./component/get-result/result.module";
import { ThanksComponent } from "./component/thank-you/thanks.component";
import { WelcomeComponent } from "./component/welcome/welcome.component";
import { InfoService } from "./services/info.service";
@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    AppRoutingModule,
    ResultModule,
    NgbModule,
    DimensionModule,
    FormsModule,
    NgxSpinnerModule,
    SelectModule,
    NgxSelectModule,
    ReactiveFormsModule
  ],
  declarations: [
    AppComponent,
    WelcomeComponent,
    ThanksComponent,
    InfoComponent
  ],
  providers: [InfoService],
  schemas: [
    NO_ERRORS_SCHEMA
  ],
  entryComponents: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
