import { Component, Input, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { NgbActiveModal } from "@ng-bootstrap/ng-bootstrap";
import { NgxSpinnerService } from "ngx-spinner";
import { InfoService } from "../../../services/info.service";
import { RadioButton } from "../invite-modal/invite";

@Component({
    selector: "app-modal",
    templateUrl: "./modal-dialog.component.html",
    styleUrls: ["./modal-dialog.component.scss"]
})
export class ModalDialogComponent implements OnInit {
    inviteLink;
    share: boolean = false;
    personal: boolean = false;
    radio = RadioButton;
    inviteDetails;
    getUserGUID;
    personalInvite: boolean = false;
    sharingInvite: boolean = false;
    @Input() note;
    @Input() email;
    constructor(
        private spinner: NgxSpinnerService,
        private router: Router,
        public activeModal: NgbActiveModal,
        private infoService: InfoService
    ) { }

    ngOnInit() {
        this.inviteDetails = JSON.parse(localStorage.getItem("invitation"));
        this.getUserGUID = localStorage.getItem("UserID");
        const sharingOptions = localStorage.getItem("share");
        if (sharingOptions === "share") {
            this.sharingInvite = true;
            this.getInviteLink();
        }
        if (sharingOptions === "personal") {
            if (this.inviteDetails.company === 1) {
                this.personal = true;
                this.share = false;
            } else {
                this.personal = false;
                this.share = true;
            }
            this.setInviteLink(this.inviteDetails, this.getUserGUID);
        }
    }

    getInviteLink() {
        setTimeout(() => {
            this.spinner.show();
            this.infoService.getInviteLink(this.getUserGUID).subscribe((getLink) => {
                const inviteURL = "inviteURL ";
                // tslint:disable-next-line:prefer-conditional-expression
                if (this.inviteDetails.company === this.radio.same) {
                    this.inviteLink = getLink[inviteURL];
                    this.share = false;
                    this.personal = true;
                    this.personalInvite = false;
                    this.spinner.hide();
                } else {
                    this.inviteLink = "www.google.com";
                    this.personal = false;
                    this.share = true;
                    this.personalInvite = false;
                    this.spinner.hide();
                }
            }, ((error) => {
                this.activeModal.close();
            }));
        }, 100);
    }

    setInviteLink(input, userGUID) {
        this.infoService.setInvite(input, userGUID).subscribe((setLink) => {
            this.personalInvite = true;
            this.inviteLink = setLink.HTML;
        }, ((error) => {
            this.activeModal.close();
        }));
    }
    removeLocalStorageData() {
        localStorage.removeItem("invitation");
        localStorage.removeItem("share");
    }
    close() {
        this.personalInvite = false;
        this.personal = false;
        this.share = false;
        this.removeLocalStorageData();
        // this.spinner.show();
        this.activeModal.close();
        // this.router.navigate(["welcome"]);
        // this.spinner.hide();
    }
    dismiss() {
        this.personalInvite = false;
        this.personal = false;
        this.share = false;
        this.spinner.show();
        this.removeLocalStorageData();
        this.activeModal.dismiss();
        this.spinner.hide();
    }
}
