import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { NgbTypeahead } from "@ng-bootstrap/ng-bootstrap";
import { NgxSpinnerService } from "ngx-spinner";
import "rxjs/add/operator/delay";
import { User } from "../../models/user";
import { InfoService } from "../../services/info.service";

@Component({
    selector: "app-info",
    templateUrl: "./info.component.html",
    styleUrls: ["./info.component.scss"]
})
export class InfoComponent implements OnInit {
    selectControl = new FormControl();
    onSubmit: boolean = false;
    user;
    userGUID;
    ifCompGUID;
    countryName;
    compSector;
    compMarket;
    compDept;
    compGUID;
    disabled: boolean = false;
    value: any = {};
    constructor(
        private spinner: NgxSpinnerService,
        private router: Router,
        private route: ActivatedRoute,
        private infoService: InfoService) {
        this.user = new User();
    }

    ngOnInit() {
        this.selectControl.valueChanges
            .subscribe((subscriptionTypeId: number) => {
                console.log(
                    "subscriptionTypeId", subscriptionTypeId
                );
            });
        const cGUID = localStorage.getItem("compGUID");
        if (cGUID !== null) {
            this.ifCompGUID = cGUID;
            this.disabled = true;
            this.getBasicInfo(this.ifCompGUID);
        } else {
            this.ifCompGUID = "";
            this.getBasicInfo(this.ifCompGUID);
        }
    }
    selected(value: any): void {
        console.log("Selected value is: ", value);
    }

    refreshValue(value: any): void {
        this.value = value;
    }

    getBasicInfo(id) {
        this.spinner.show();
        this.infoService.getBasicInfo(id).subscribe((res) => {

            if (this.ifCompGUID) {
                this.user.compGUID = this.ifCompGUID;
                this.user.location = res.CompCountryId;
                this.user.companyType = res.CompMarketId;
                this.user.coSector = res.CompSectorId;
                this.user.userNewsletter = false;
                this.user.userFirstName = "";
                this.user.userLastName = "";
                this.user.userEmail = "";
                this.user.compName = res.CompName;
                this.user.compEmployees = res.CompEmployees;
                this.user.userBirthYear = "";
                this.user.userFunction = "";
                this.user.userDepartmentId = "";
            } else {
                this.user.compGUID = "";
                this.user.location = "1";
                this.user.companyType = "1";
                this.user.coSector = "1";
                this.user.compEmployees = "";
                this.user.userFirstName = "";
                this.user.userLastName = "";
                this.user.userEmail = "";
                this.user.compName = "";
                this.user.userDepartmentId = "1";
                this.user.userNewsletter = false;
                this.user.userBirthYear = "";
                this.user.userFunction = "";
            }
            this.countryName = res.OptionsCountry;
            this.compSector = res.OptionsSector;

            this.compMarket = res.OptionsMarket;
            this.compDept = res.OptionsDepartment;
            this.spinner.hide();
        });
    }

    sector(args) {
        console.log(args);
        console.log(this.user.coSector);
    }

    submit() {
        this.onSubmit = true;
    }

    navigateToDimension() {
        // tslint:disable-next-line:prefer-conditional-expression
        if (this.user.userNewsletter === true) {
            this.user.userNewsletter = 1;
        } else {
            this.user.userNewsletter = 0;
        }
        this.spinner.show();
        this.infoService.setBasicInfo(this.user).subscribe((res) => {
            const uGUID = "UserGUID ";
            this.userGUID = res[uGUID];
            localStorage.setItem("UserID", (this.userGUID));
            this.spinner.hide();
            this.router.navigate(["intro/2"]);
        }, (error) => {
            console.log(error);
            this.spinner.hide();
        });
    }
}
